.exercisevideos {
    margin-top: 45px;
    padding: 20px;
    &_title {
        margin-bottom: 60px;
        text-align: center;
        text-transform: capitalize;
        font-size: 40px;
        font-weight: normal;
        span {
            color: #ff2625;
        }
    }
    &_box {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        align-items: center;
        gap: 100px;
        h5, h6 {
            font-weight: 200;
            margin-top: 8px;
            color: #000;
            span {
                color: #891212;
            }
        }
        h5 {
            font-size: 24px;
        }
        h6 {
            font-size: 18px;
        }
    }
    &_video {
        display: flex;
        flex-direction: row;
        gap: 24px;
        width: 387px;
        height: 381px;
        text-decoration: none;
    }
}

.exercise_video {
    width: 400px;
    height: 400px;
    text-decoration: none;
    transition: 0.4s all ease-in-out;
    img {
        width: 100%;
    }
    &:hover {
        transform: scale(1.1, 1.1);
    }
}

@media (max-width: 1440px) {
    .exercise_video {
        width: 350px;
        height: 350px;
    }
}

@media (max-width: 1290px) {
    .exercise_video {
        width: 300px;
        height: 300px;
    }
    .exercisevideos {
        &_box {
            gap: 80px;
            h5 {
                font-size: 20px;
            }
            h6 {
                font-size: 14px;
            }
        }
    }
}

