.detail {
    gap: 60px;
    display: flex;
    flex-direction: row;
    padding: 20px;
    align-items: center;
    &-image {
        width: 729px;
        height: 742px;
    }
    &_text {
        gap: 35px;
        &-title {
            font-size: 48px;
            text-decoration: none;
            text-transform: capitalize;
        }
        &-subtitle {
            padding-top: 100px;
            font-size: 24px;
            font-weight: 300;
            line-height: 36px;
        }
    }
    &_extra {
        display: flex;
        flex-direction: row;
        gap: 24px;
        align-items: center;
        &-button {
            border: none;
            border-radius: 50% !important; 
            margin-left: 0 !important; 
            margin-top: 25px !important;
            background: #fff2db;
            width: 100px;
            height: 100px;
            img {
                width: 50px;
                height: 50px;
            }
            &:hover {
                background: #fbe5bf;
            }
        }
        &-descr {
            padding-top: 20px;
            text-transform: capitalize;
            font-size: 19px;
            font-weight: 400;
        }
    }
}

@media (max-width: 1350px) {
    .detail {
        gap: 40px;
        &-image {
            width: 710px;
            height: 730px;
        }
    }
}

@media (max-width: 1300px) {
    .detail {
        &-image {
            width: 650px;
            height: 670px;
        }
        &_text {
            &-title {
                font-size: 44px;
            }
            &-subtitle {
                padding-top: 50px;
            }
        }
        div {
            row-gap: 20px;
            &_extra {
                button {
                    margin-top: 0 !important;
                }
            }
        }
    }
}

@media (max-width: 1300px) {
    .detail {
        &-image {
            width: 570px;
            height: 590px;
        }
        &_text {
            &-title {
                font-size: 40px;
            }
            &-subtitle {
                padding-top: 40px;
                font-size: 22px;
            }
        }
        div {   
            &_extra {
                gap: 20px;
                button {
                    margin-top: 0 !important;
                }
            }
        }
    }
}

@media (max-width: 992px) {
    .detail {
        &-image {
            width: 450px;
            height: 470px;
        }
        &_text {
            &-title {
                font-size: 38px;
            }
            &-subtitle {
                padding-top: 30px;
                font-size: 20px;
            }
        }
    }
}

@media (max-width: 850px) {
    .detail {
        flex-direction: column;
        &-image {
            width: 550px;
            height: 570px;
        }
        &_text {
            &-title {
                text-align: center;
                font-size: 38px;
            }
            &-subtitle {
                text-align: center;
                font-size: 20px;
            }
        }
        &_extra {
            justify-content: center;
            &-button {
                width: 80px;
                height: 80px;
                img {
                    width: 40px;
                    height: 40px;  
                }
            }
            &-descr {
                font-size: 18px;
            }
        }
    }
    .one {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

@media (max-width:700px) {
    .detail {
        &-image {
            width: 445px;
            height: 445px;
        }
        &_text {
            &-title {
                font-size: 32px;
            }
            &-subtitle {
                font-size: 16px;
            }
        }
        &_extra {
            &-button {
                width: 60px;
                height: 60px;
                img {
                    width: 30px;
                    height: 30px;  
                }
            }
            &-descr {
                font-size: 14px;
            }
        }
    }
}

@media (max-width:570px) {
    .detail {
        &-image {
            width: 400px;
            height: 400px;
        }
        &_text {
            &-title {
                font-size: 28px;
            }
            &-subtitle {
                padding-top: 15px;
                font-size: 16px;
                line-height: 24px;
            }
        }
        &_extra {
            &-button {
                width: 60px;
                height: 60px;
                margin: 0 !important;
                img {
                    width: 30px;
                    height: 30px;  
                }
            }
            &-descr {
                font-size: 14px;
            }
        }
    }
    .one {
        flex-direction: column;
        margin-top: 15px;
    }
}

@media (max-width:460px) {
    .detail {
        &-image {
            width: 300px;
            height: 300px;
        }
        &_text {
            &-title {
                font-size: 24px;
            }
            &-subtitle {
                padding-top: 10px;
                font-size: 13px;
            }
        }
        &_extra {
            &-button {
                width: 50px;
                height: 50px;
                img {
                    width: 25px;
                    height: 25px;  
                }
            }
        }
    }
}