.footer {
    margin-top: 80px;
    background-color: #fff3f4;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px; //
    padding: 25px;
    img {
        width: 200px;
        height: 40px;
    }
}

@media (max-width: 600px) {
    .footer {
        height: 70px;
        img {
            width: 150px;
            height: 35px;
        }
    }
}