.herobanner {
    margin-top: 202px;
    position: relative;
    padding: 0 30px;
    min-height: 600px;
    h1 {
        color: #FF2625;
        font-weight: 700;
        font-size: 26px;
    }
    &__p-main {
        font-weight: 700;
        font-size: 44px;
        margin-bottom: 23px;
        margin-top: 30px;
    }
    &__p {
        font-size: 22px;
        line-height: 35px;
        margin-bottom: 30px;
    }
    &__p-last {
        font-weight: 600;
        position: absolute;
        bottom: -50px;
        font-size: 200px;
        color: #FF2625;
        opacity: 0.1;
    }
    &-img {
        position: absolute;
        right: 40px;
        top: 0px;
        width: 700px;
        height: 900px;
        margin-top: -285px;
        z-index: 10000;
        border-bottom-left-radius: 150px;
    }
    &__button {
        appearance: none;
        border: 0;
        border-radius: 4px;
        background: #FF2625;
        color: #fff;
        padding: 10px 20px;
        font-size: 16px;
        margin: 1px;
        text-decoration: none;
        &:hover {
            background: #ff4545;
        }
    }  
}


@media (max-width:1450px) {
    .herobanner {
        &-img {
            width: 600px;
            height: 800px;
            margin-top: -285px;
        }
        &__p-last {
            bottom: 41px;
        }
    }
}

@media (max-width:1200px) {
    .herobanner {
        margin-top: 100px;
        h1 {
            font-size: 22px;
        }
        &__p-main {
            font-size: 40px;
            margin-bottom: 20px;
            margin-top: 25px;
        }
        &__p {
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 25px;
            max-width: 400px;
        }
        &-img {
            width: 475px;
            height: 675px;
            margin-top: -182px;
        }
        &__p-last {
            bottom: 69px;
            font-size: 178px;
        }
    }
}

@media (max-width: 992px) {
    .herobanner {
        min-height: 510px;
        h1 {
            font-size: 18px;
        }
        &__p-main {
            font-size: 30px;
        }
        &__p {
            font-size: 18px;
            max-width: 350px;
        }
        &-img {
            width: 425px;
            height: 625px;
            margin-top: -182px;
        }
        &__p-last {
            bottom: 40px;
            font-size: 150px;
        }
    }
}

@media (max-width: 850px) {
    .herobanner {
        min-height: 380px;
        text-align: center;
        &__p {
            max-width: 100%;
        }
        &-img {
            display: none;
        }
        &__p-last {
            display: none;
        }
    }
}

@media (max-width: 520px) {
    .herobanner {
        h1 {
            font-size: 24px;
        }
        &__p-main {
            font-size: 26px;
        }
        &__p {
            font-size: 16px;
        }
    }
}


