.bodypart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #fff;
    text-decoration: none;
    cursor: pointer;
    img {
        width: 40px;
        height: 40px;
        
    }
    &-card {
        transform: scale(1, 1);
        transition: 0.3s all ease-in-out;
        border: none;
    }
    &_title {
        font-weight: 900;
        color: #3A1212;
        text-transform: capitalize;
        padding-top: 40px;
    }
    &_active {
        border-top: 4px solid #ff2625;
    }
}

@media (max-width: 800px) {
    .bodypart {
        img {
            width: 25px;
            height: 25px;
        }
        &_title {
            font-weight: 500;
            padding-top: 15px;
        }
    }
}