.searchexercises {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 120px;
    justify-content: center;
    padding: 20px;
    &_p {
        font-weight: 700;
        font-size: 44px;
        text-align: center;
        margin-bottom: 50px;
    }
    &_box {
        position: relative;
        margin-bottom: 72px;
        input {
            padding: 3px 10px;
            height: 48px;
            width: 800px;
            background-color: #ffffff;
            font-weight: 700;
            border: 1px solid gray;
            border-radius: 5px;
        }
    }
    &_button {
        text-transform: none;
        width: 175px !important;
        height: 48px;
        font-size: 20px;
        position: absolute;
        border-radius: 5px;
        right: -1px;
        top: -1px;
    }
    &__list {
        position: relative;
        width: 100%;
        padding: 20px;
    }
}

@media (max-width: 1240px) {
    .searchexercises {
        margin-top: 0;
        padding-top: 0;
    }
}

@media (max-width: 992px) {
    .searchexercises {
        &_p {
            font-size: 40px;
            margin-bottom: 40px;
        }
        &_box {
            margin-bottom: 42px;
            input {
                padding: 2px 8px;
                height: 38px;
                width: 600px;
            }
        }
        &_button {
            width: 100px !important;
            height: 38px;
            font-size: 16px;
        }
    }
}

@media (max-width: 700px) {
    .searchexercises {
        &_p {
            font-size: 24px;
            margin-bottom: 30px;
        }
        &_box {
            margin-bottom: 32px;
            input {
                padding: 1px 6px;
                height: 28px;
                width: 400px;
            }
        }
        &_button {
            width: 80px !important;
            height: 28px;
            font-size: 12px;
            padding: 7px 20px;
        }
    }
}

@media (max-width: 520px) {
    .searchexercises {
        &_p {
            font-size: 18px;
            margin-bottom: 25px;
        }
        &_box {
            margin-bottom: 30px;
            input {
                padding: 1px 6px;
                height: 24px;
                width: 300px;
            }
        }
        &_button {
            width: 60px !important;
            height: 24px;
            font-size: 12px;
            padding: 4px 10px;
        }
    }
}