.exercisecard {
    width: 400px;
    height: 445px;
    background: #fff;
    border-top: 4px solid #FF2625;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-bottom: 10px;
    &_exercisename {
        margin-left: 21px;
        color: #000;
        font-weight: bold;
        margin-top: 11px;
        padding-bottom: 10px;
        text-transform: capitalize;
        font-size: 22px;
        text-align: center;
    }
    img {
        height: 326px;
    }
    &_card {
        &-first {
            background: #ffa9a9;
            &:hover {
                background: #fa7474;
            } 
        }
        &-second {
            background: #fcc757;
            &:hover {
                background: #f9b72a;
            } 
        }
    }
}

.nicebutton {
    cursor: pointer;
    border: none;
    padding: 15px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    margin-left: 21px;
    color: #fff;
    font-size: 14px;
    border-radius: 20px;
    text-transform: capitalize;
}

.exercisecards:hover {
    transform: scale(1.1, 1.1);
}
.exercisecards {
    padding: 25px;
}

@media (max-width: 1400px) {
    .exercisecard {
        width: 350px;
        height: 400px;
    }
}

@media (max-width: 1240px) {
    .exercisecard {
        width: 300px;
        height: 350px;
        padding: 0;
        &_exercisename {
            margin-left: 17px;
            margin-top: 9px;
            padding-bottom: 8px;
            font-size: 18px;
            text-align: center;
        }
        img {
            margin: 0 auto;
            width: 200px;
            height: 220px;
            
        }
        &_card {
            button {
                padding: 12px;
                font-size: 12px;
            }
        }
    }

    .exercisecards {
        padding: 15px;
    }
}

@media (max-width: 640px) {
    .exercisecard {
        width: 280px;
        height: 330px;
        padding: 0;
        &_exercisename {
            margin-left: 15px;
            margin-top: 6px;
            padding-bottom: 5px;
            font-size: 17px;
        }
        img {
            width: 190px;
            height: 210px;
            
        }
        &_card {
            button {
                padding: 11px;
                font-size: 11px;
            }
        }
    }
}