.horisontalScrollbar {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    // background-color: aqua;
    &_box {
        display: flex;
        margin: 10px;
        width: 240px;
        height: 240px;
        text-align: center;
        &:hover {
            transform: scale(1.1, 1.1);
        }
    }
}

@media (max-width:1450px) {
    .horisontalScrollbar {
        &_box {
            width: 200px;
            height: 200px;
        }
    }
}

@media (max-width:992px) {
    .horisontalScrollbar {
        &_box {
            width: 150px;
            height: 150px;
        }
    }
}

@media (max-width: 800px) {
    .horisontalScrollbar {
        &_box {
            width: 100px;
            height: 100px;
            margin: 5px;
        }
    }
}