#exercises {
    margin-top: 110px;
    padding: 20px;
    h3 {
        margin-bottom: 46px;
        font-size: 48px;
        font-weight: 500;
        text-align: center;
    }
}

.exercise {
    &_firstdiv {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media (max-width: 1240px) {
    #exercises {
        h3 {
            font-size: 44px;
        }
    }
}

@media (max-width: 640px) {
    #exercises {
        h3 {
            font-size: 36px;
            margin-bottom: 18   px;
        }
    }
}

