.header {
    display: flex;
    margin-top: 20px;
    width: 45%;
    justify-content: space-between;
    &__navbar {
        display: flex;
        align-items: center;
        a:last-child {
            padding-left: 30px;
            margin-right: 10px;
            text-decoration: none;
            color: #3A1212;
            a {
                border-bottom: 0;
            }
        }
        a {
            font-size: 24px;
        }
        a:first-child {
            text-decoration: none;
            color: #3A1212;
            border-bottom: 3px solid #FF2625;
        }
    }
    &__logo {
        width: 58px;
        height: 58px;
        margin: 0 20px;
        justify-content: center;
    }
}

@media (max-width: 992px) {
    .header {
        width: 35%;
        &__navbar {
            a:first-child, a:last-child {
                font-size: 20px;
            }
        }
    }
}

@media (max-width: 850px) {
    .header {
        width: 100%;
        padding-right: 44%;
        &__navbar {
            a:first-child, a:last-child {
                font-size: 20px;
            }
        }
    }
}

@media (max-width: 520px) {
    .header {
        width: 100%;
        padding-right: 30%;
        &__navbar {
            a:first-child, a:last-child {
                font-size: 20px;
            }
        }
    }
}